import React, { useEffect, useState } from 'react';

import { useParams } from 'react-router-dom';

import Sitemap from 'containers/Sitemap/Sitemap';
import Sidebar from 'containers/Sidebar/Sidebar';

import classes from './Project.module.scss';
import Library from 'containers/Library/Library';

enum Type {
	NEW_PAGE,
	NEW_COMPONENT,
	MODIFY_PAGE,
	MODIFY_COMPONENT,
	DELETE_PAGE,
	DELETE_COMPONENT
}

interface Params {
	id: string;
}

interface Component {
	id: number;
	name: string;
	content: object;
}

interface Page {
	id: number;
	name: string;
	url: string;
	components: {
		id: number;
		name: string;
	}[];
}

/*

	** Website structure: **


	// Pages

	[
		{
			id: number;
			name: string;
			url: string;
			components: [componentId: number, ...]
		},
		...
	]

	// Components

	[
		{
			id: number;
			name: string;
			styleId: number;
			content: {
				name: string;
				...
			}
		},
		...
	]

	// Styles

	[
		{
			id: number;
			styles: {
				main: {
					fontFamily: string;
				},
				meta: {
					fontSize: number;
				}
			}
		},
		...
	]

*/

const Project: React.FC = () => {
	const [pages, setPages] = useState<Page[]>([]);
	const [components, setComponents] = useState<Component[]>([]);

	const [selectedPage, setSelectedPage] = useState<number | null>(null);
	const [selectedComponent, setSelectedComponent] = useState<number | null>(null);

	const [type, setType] = useState<Type | null>(null);

	const { id } = useParams<Params>();

	useEffect(() => {
		fetchData();
	}, []);

	useEffect(() => {
		// const activePageComponents = pages.find((p) => p.id === selectedPage)!.components;
		// const activeComponents = components.filter((component) =>
		// 	activePageComponents.includes((c) => c.id === component.id)
		// );
	}, [selectedPage]);

	const fetchData = async () => {
		const sampleSitemap: Page[] = [
			{
				id: 1,
				name: 'Home',
				url: '/',
				components: [
					{
						id: 1,
						name: 'Banner'
					},
					{
						id: 2,
						name: 'Interstitial'
					}
				]
			},
			{
				id: 2,
				name: 'About',
				url: '/about',
				components: [
					{
						id: 1,
						name: 'Banner'
					}
				]
			}
		];

		setPages(sampleSitemap);
	};

	const pageClickHandler = (id: number) => {
		setSelectedPage((prevState) => {
			setSelectedComponent(null);

			if (prevState === id) {
				setType(null);

				return null;
			} else {
				setType(Type.MODIFY_PAGE);

				return id;
			}
		});
	};

	const componentClickHandler = (id: number) => {
		setSelectedComponent((prevState) => {
			if (prevState === id) {
				setType(Type.MODIFY_PAGE);

				return null;
			} else {
				setType(Type.MODIFY_COMPONENT);

				return id;
			}
		});
	};

	const newPageHandler = () => {
		setType(Type.NEW_PAGE);

		setSelectedPage(null);
	};

	const newComponentHandler = () => {
		setType(Type.NEW_COMPONENT);

		setSelectedComponent(null);
	};

	const hideSidebarHandler = () => {
		setType(null);
	};

	return (
		<div className={classes.Project}>
			<Sitemap
				data={pages}
				name='Ashk Aesthetics'
				category='Beauty Salon'
				selectedPage={selectedPage}
				selectedComponent={selectedComponent}
				onPageClick={pageClickHandler}
				onComponentClick={componentClickHandler}
				onNewPage={newPageHandler}
				onNewComponent={newComponentHandler}
			/>

			<Sidebar visible={type != null} type={type} onCancel={hideSidebarHandler} />

			{/* <Preview content={components} /> */}
		</div>
	);
};

export default Project;
